.App {
  width: 100%;
  height: 100%;
}
.video-tab {
  width: 80%;
  margin: 2rem auto;
  display: grid;
  align-content: center;
  justify-content: center;
  align-items: center;
  justify-items: center;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
